/* ボタン共通パーツ */
.button a {
  display: block;
  max-width: 15.625rem;
  width: 100%;
  padding: 1rem 5rem;
  background-color: #024892;
  color: #fff;
  font-size: 1.125rem;
  font-weight: 700;
  transition: opacity 0.3s;
  position: relative;
}

.button a::before {
  position: absolute;
  content: "";
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  right: 5%;
  top: 50%;
  transform: translate(-70%, -50%) rotate(45deg);
  border-top: 1px solid #fff;
  border-right: 1px solid #fff;
}
@media screen and (min-width: 768px) {
  .button a::before {
    right: 5%;
    transform: translate(-60%, -50%) rotate(45deg);
  }
}

